@font-face {
  font-family: Avenir;
  src: url("./fonts/Avenir.ttc");
}

@font-face {
  font-family: Avenir-Heavy;
  font-weight: 700;
  src: url("./fonts/Avenir-Heavy.ttf");
}

* {
  box-sizing: border-box;
}

html {
  font-weight: 300;
  font-family: Avenir, "Helvetica", sans-serif;
}

.form {
  position: relative;
}

.form::before {
  content: "";
  position: absolute;
  display: inline-block;
  background-image: url(./images/Search.svg);
  background-repeat: no-repeat;
}

.form-big::before {
  left: 26px;
  top: 25px;
  width: 20px;
  height: 20px;
}

.form-small::before {
  left: 13px;
  top: 15px;
  background-size: 11px;
  width: 11px;
  height: 11px;
}

.search-bar {
  display: block;
  width: 100%;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  font-family: inherit;
  margin-top: 20px;
}

.search-bar-small {
  padding-left: 33px;
  height: 42px;
  font-size: 14px;
}

.search-bar-big {
  padding: 25px 0 25px 62px;
  height: 70px;
  font-size: 16px;
}

.search-bar:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search-bar:focus {
  outline: none;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  font-weight: bold;
  font-family: Avenir-Heavy, "Helvetica", sans-serif;
}
